@font-face {
    font-family: Montserrat;
    src: url(./assets/fonts/Montserrat_Bold.ttf);
    font-weight: bold;
}
@font-face {
    font-family: Montserrat;
    src:url(./assets/fonts/Montserrat_Regular.ttf);
    font-weight: normal;
}
@font-face {
    font-family: Montserrat;
    src: url(./assets/fonts/Montserrat_Light.ttf);
    font-weight: initial;
}

.lg{
    font-size: 150%;
}
.md{
    font-size: 100%;
}
.sm{
    font-size: 70%;
}

.giga{
    font-weight: bold;
    font-size: 500%;
}


body{
    overflow-x: hidden;
    overflow-y: hidden;
    font-family: Montserrat;
}

h1{
    margin: 0;
}

@media (max-width: 1000px) {
    body{
        overflow-y: auto;
    }
}